
























































































import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import { LocationEntiry } from '@/services/entities/location'
import LocationRepository from '@/services/repositories/location'
import { assign, debounce, includes, size, trim } from 'lodash'
import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'

const beReport = namespace('beReport')

@Component({ components: {} })
export default class FiltersPopupPl9i extends Mixins(PopupMixin) {
  @beReport.Getter('getFilter') getFilter!: (rerport: 'pl9a' | 'pl9b' | 'pl9c' | 'pl9d' | 'pl9e' | 'pl9f' | 'pl9g' | 'pl9h') => { report: string; filters: Record<string, any> }

  isLoading = false

  filters: Record<string, any> = {
    month: new Date().getMonth() + 1,
    year: new Date()
  }

  locationRes: LocationRepository | undefined = undefined

  options: Array<any> = []

  areas: LocationEntiry[] = []
  branches: LocationEntiry[] = []
  offices: LocationEntiry[] = []

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  validateFilterForm = (rule?: any, value?: any, callback?: any) => {
    if (rule.field === 'numberRule' && Number(this.filters.fromRevenue) > Number(this.filters.toRevenue)) {
      callback(new Error(`Giá trị "Từ" phải nhỏ hơn "Đến"`))
    }
    if (rule.field === 'commission' && Number(this.filters.fromCommission) > Number(this.filters.toCommission)) {
      callback(new Error(`Giá trị "Từ" phải nhỏ hơn "Đến"`))
    }
    if (rule.field === 'revenue' && Number(this.filters.fromValue) > Number(this.filters.toValue)) {
      callback(new Error(`Giá trị "Từ" phải nhỏ hơn "Đến"`))
    }

    callback()
  }

  filterRules = {
    numberRule: [{ validator: this.validateFilterForm, trigger: 'blur' }],
    commission: [{ validator: this.validateFilterForm, trigger: 'blur' }],
    revenue: [{ validator: this.validateFilterForm, trigger: 'blur' }]
  }

  query = {
    areas: {
      i: 0,
      ps: 20,
      search: ''
    },
    branches: {
      i: 0,
      ps: 20,
      search: ''
    },
    offices: {
      i: 0,
      ps: 20,
      search: ''
    }
  }

  remoteMethodGetArea(query: string): void {
    this.isLoading = true
    this.query.areas.search = query
    this.query.areas.i = 0
    this.query.areas.ps = 20
    const a = debounce(this.searchArea, 500)
    a(query)
  }

  remoteMethodGetBranch(query: string): void {
    this.isLoading = true
    this.query.branches.search = query
    this.query.branches.i = 0
    this.query.branches.ps = 20
    const a = debounce(this.searchBranch, 500)
    a(query)
  }

  remoteMethodGetOffice(query: string): void {
    this.isLoading = true
    this.query.offices.search = query
    this.query.offices.i = 0
    this.query.offices.ps = 20
    const a = debounce(this.searchOffice, 500)
    a(query)
  }

  loadAreas(): void {
    console.log(1)
    if (size(this.areas) === this.query.areas.i + this.query.areas.ps) {
      this.query.areas.i = size(this.areas)
      const a = debounce(this.searchArea, 500)
      a(this.query.areas.search, true)
    }
  }

  loadBranches(): void {
    console.log(1)
    if (size(this.branches) === this.query.branches.i + this.query.branches.ps) {
      this.query.branches.i = size(this.branches)
      const a = debounce(this.searchBranch, 500)
      a(this.query.branches.search, true)
    }
  }
  loadOffices(): void {
    console.log(1)
    if (size(this.offices) === this.query.offices.i + this.query.offices.ps) {
      this.query.offices.i = size(this.offices)
      const a = debounce(this.searchOffice, 500)
      a(this.query.offices.search, true)
    }
  }

  async searchArea(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.areas = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('area', {
        search: trim(search),
        i: this.query.areas.i,
        ps: this.query.areas.ps
      })
      if (isAdd) {
        const a = result || []
        this.areas = [...this.areas, ...a]
      } else {
        this.areas = result || []
      }

      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.areas = []
    }
  }

  async searchBranch(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.branches = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('branch', {
        search: trim(search),
        i: this.query.branches.i,
        ps: this.query.branches.ps
      })

      if (isAdd) {
        const a = result || []
        this.branches = [...this.branches, ...a]
      } else {
        this.branches = result || []
      }
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.branches = []
    }
  }

  async searchOffice(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.offices = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('office', {
        search: trim(search),
        i: this.query.offices.i,
        ps: this.query.offices.ps
      })

      if (isAdd) {
        const a = result || []
        this.offices = [...this.offices, ...a]
      } else {
        this.offices = result || []
      }
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.offices = []
    }
  }

  resetData(): void {
    //@ts-ignore
    this.$refs.filterForm.clearValidate()

    this.filters = {
      month: new Date().getMonth() + 1,
      year: new Date(),
      officeIds: [],
      branchIds: [],
      areaIds: [],
      toValue: null,
      fromValue: null,
      selectMonthRevenue: null
    }
  }

  handleSubmit(type: 'reset' | 'submit'): void {
    if (type === 'reset') {
      this.resetData()
      return
    }
    let form: any = this.$refs.filterForm
    form.validate((valid: any) => {
      if (valid) {
        //   this.$emit('change', this.filters)
        this.setOpenPopup({
          popupName: 'popup-filter-9i',
          isOpen: false
        })
      }
    })
  }

  reset(): void {
    this.areas = []
    this.branches = []
    this.offices = []
  }

  handleOpen(): void {
    console.log('open')
    this.searchArea('')
    this.searchBranch('')
    this.searchOffice('')
  }

  handleClose(): void {
    this.reset()
  }

  created(): void {
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })

    //   const filters = this.getFilter(this.type)
    //   if (filters) {
    //     this.filters = assign(this.filters, filters.filters)
    //     delete this.filters.search
    //     delete this.filters.sortType
    //   }
  }
}
