import { Component, Vue } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import { UserEntity } from '@/services/entities/user'
const beUser = namespace('beUser')
@Component
export default class RoleKpiMixin extends Vue {
  @beUser.State('user') user!: UserEntity
  @beUser.State('role') roleMembers!: any
  get memberLogin(): any {
    return this.user
  }
  get branchId(): number {
    return this.user.branchId
  }
  get memberId(): number {
    return this.user.id
  }
  get brandName(): string {
    return this.user.branchName
  }
  get memberFullName(): string {
    return this.user.fullName
  }

  get roleHOLEADERAmin(): boolean {
    let role = false
    if (this.user.role) {
      if (this.roleMembers.userRole.includes('HO_LEADER') || this.roleMembers.userRole.includes('HO_MEMBER') || this.roleMembers.userRole.includes('ADMIN')) {
        role = true
      }
    }
    return role
  }
  get roleSaleMember(): boolean {
    let role = false
    if (
      this.roleMembers.userRole.includes('TRANSACTION_OFFICE_MEMBER') &&
      !this.roleOfficeLeaderMember &&
      !this.roleBranchMember &&
      !this.roleBranchLeader &&
      !this.roleHOLEADERAmin
    ) {
      role = true
    }
    return role
  }
  get roleOfficeLeaderMember(): boolean {
    let role = false
    if (this.roleMembers.userRole.includes('TRANSACTION_OFFICE_LEADER') && !this.roleBranchMember && !this.roleBranchLeader && !this.roleHOLEADERAmin) {
      role = true
    }
    return role
  }

  get roleBranchMember(): boolean {
    let role = false
    if (this.roleMembers.userRole.includes('BRANCH_MEMBER') && !this.roleBranchLeader && !this.roleHOLEADERAmin) {
      role = true
    }
    return role
  }
  get roleBranchLeader(): boolean {
    let role = false
    if (this.roleMembers.userRole.includes('BRANCH_LEADER') && !this.roleHOLEADERAmin) {
      role = true
    }
    return role
  }
}
