























































































































































































































































import { PaginationInterface } from '@/interface'
import getRepository from '@/services'
import { Pl9eReportEntity } from '@/services/entities/report'
import ReportRepository from '@/services/repositories/report'
import { assign, forEach, includes, debounce } from 'lodash'
import { Component, Mixins } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import ReportFilter from '../components/filters/FilterPl9i.vue'
import ExportMixin from '../mixins/exportsKpi'
import RoleKpiMixin from '../mixins/roleKpi'
const beAudit = namespace('beAudit')

@Component({ components: { ReportFilter } })
export default class Pl9iReport extends Mixins(ExportMixin, RoleKpiMixin) {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }

  data: Pl9eReportEntity[] = []

  filters: Record<string, any> = {
    kpiMonth: new Date().getMonth() + 1,
    kpiYear: new Date(),
    kpiCode: 'REVENUE_YEAR1'
  }

  table: PaginationInterface = {
    total: 0,
    page: 1,
    limit: 20
  }

  query: Record<string, any> = {
    pageNo: 1,
    pageSize: 20
  }

  isLoading = false

  tableHeight: string | number = 'auto'

  reportRes: ReportRepository | undefined = undefined

  summary: Record<string, any> = {}

  get getScreen(): string {
    if (window.screen.availWidth >= 1600) {
      return 'full'
    }
    if (window.screen.availWidth > 1366 && window.screen.availWidth < 1600) {
      return 'hd'
    }
    return 'low'
  }

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  handleLoad(tree: Pl9eReportEntity, treeNode: any, resolve: any): void {
    setTimeout(async () => {
      resolve([])
    }, 500)
  }

  async init(): Promise<void> {
    if (this.isLoading) {
      return
    }
    try {
      this.isLoading = true
      //@ts-ignore
      // const filters = this.getFilter(this.$route.name)
      // if (filters) {
      //   this.filters = assign(this.filters, filters.filters)
      // }
      let params = {
        ...this.filters
      }
      // if (this.roleSaleMember) {
      //   delete params.memberIdArray
      // }
      if (this.roleOfficeLeaderMember) {
        delete params.areaIdArray
        delete params.branchIdArray
        delete params.transOfficeIdArray
      }
      if (this.roleBranchMember || this.roleBranchLeader) {
        delete params.areaIdArray
        delete params.branchIdArray
      }
      // if (this.roleBranchLeader) {
      //   delete params.areaIdArray
      //   delete params.branchIdArray
      // }
      this.getData(params)
      this.getSummary(params)
    } catch (error) {
      this.isLoading = false
    }
  }
  async getData(params: Record<string, any>): Promise<any> {
    try {
      const type = 'branch'
      this.data = await this.reportRes?.getReportByType(type, assign(params, this.query))
    } catch (error) {
      console.log(error)
    }
  }
  async getSummary(params: Record<string, any>): Promise<any> {
    try {
      const type = 'branch'
      const result = await this.reportRes?.getSummaryByType(type, assign(params, this.query))
      this.summary = result || {}
      this.table.total = result.total
      console.log('result', result)
      this.isLoading = false
    } catch (error) {
      console.log(error)
    }
  }

  summaryMethod(params: { columns: any; data: Pl9eReportEntity[] }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 2) {
        sums[index] = ''
        return
      }
      if (index === 2) {
        sums[index] = 'Tổng'
        return
      }
      const a = this.summary[column.property] || 0
      sums[index] = `${
        //@ts-ignore
        includes(column.property, 'Percent') || includes(column.property, 'percent') ? this.$options.filters.formatPercent(a) : this.$options.filters.formatNumber(a)
      }`
      // const value = column.property
      // if (value === 'percent') {
      //   sums[index] = '31%'
      // } else {
      //   sums[index] = '121,211,221,003'
      // }
    })
    return sums
  }

  handleSizeChange(val: number): void {
    this.table.limit = val
    this.table.page = 1
    this.query.pageNo = 1
    this.query.pageSize = val
    this.init()
    // this.getSummary()
  }

  handleCurrentChange(val: number): void {
    this.table.page = val
    this.query.pageNo = val

    this.init()
    // this.getSummary()
  }

  handleFiltersChange(filters: Record<string, any>): void {
    console.log('filter', filters)

    this.filters = {
      ...filters
    }
    this.table.page = 1
    this.query.pageNo = 1

    this.debounceFilter()
  }

  debounceFilter = debounce(() => {
    this.init()
    // this.getSummary()
  }, 600)

  getHeightOfTable(): void {
    this.tableHeight = `${window.innerHeight - 233}px`
  }

  mounted(): void {
    // this.init()
    this.getHeightOfTable()
  }

  created(): void {
    this.reportRes = getRepository('report', {
      districtId: this.districtId
    })
    // this.getSummary()
  }

  handleExportFile(type: any, format: any, filters: Record<string, any>, table?: Record<string, any>): void {
    if (this.data.length === 0) {
      this.$message.error('Không tìm thấy dữ  liệu xuất file')
    } else {
      this.handleExport(type, format, filters, table)
    }
  }
}
