import { assign } from 'lodash'
import getRepository from '@/services'
import ReportRepository from '@/services/repositories/report'
import { Vue, Component } from 'vue-property-decorator'
// import { filter } from 'vue/types/umd'

@Component
export default class ExportMixin extends Vue {
  reportRes2: ReportRepository | undefined = undefined

  isExcelLoading = false
  isPdfLoading = false

  async handleExport(type: 'member' | 'branch', format: 'excel' | 'pdf' = 'excel', filters: Record<string, any>, table?: Record<string, any>): Promise<void> {
    if (format === 'excel') this.isExcelLoading = true
    if (format === 'pdf') this.isPdfLoading = true
    try {
      const response = await this.reportRes2?.exportKpi(
        type,
        {
          ...filters,
          ext: format == 'excel' ? 'xlsx' : 'pdf'
        }
        // assign(
        //   {
        //     format: format
        //   },
        //   filters,
        //   table
        // )
      )
      const url = window.URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      const date = new Date()
      const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}${date.getHours()}${date.getMinutes()}${date.getMilliseconds()}`
      // link.setAttribute('download', `${type.toUpperCase()}_${time}.${format === 'pdf' ? 'pdf' : 'xlsx'}`)
      link.setAttribute('download', `KPI_${type == 'member' ? 'CBNV' : 'DVKD'}_${time}.${format === 'pdf' ? 'pdf' : 'xlsx'}`)

      document.body.appendChild(link)
      link.click()
      this.isExcelLoading = false
      this.isPdfLoading = false
    } catch (error) {
      if (error.response.status === 404) {
        this.$message.error('Không tìm thấy dữ liệu xuất file')
      }
      this.isExcelLoading = false
      this.isPdfLoading = false
    }
  }

  created(): void {
    this.reportRes2 = getRepository('report', {
      districtId: this.districtId
    })
  }
}
